        </div>
        <Clock :stage="stage" :timer="timer" :timerRunning="runTimer" @stageEnd="nextStage" />
        <TodoList />
        <div id="buttonsWrapper" class="flex justify-center pb-5">
            <Button v-if="!runTimer" @clicked="startTimer">Start Timer</Button>
            <Button v-if="runTimer" @clicked="pauseTimer">Pause Timer</Button>
        </div>
    </div>
</template>

<script>
import TodoList from '@/components/TodoList.vue';
import Button from '@/components/Button.vue';
import Clock from '@/components/Clock.vue';

export default {
  name: 'App',
  components: {
      TodoList,
      Button,
      Clock,
  },
  data() {
      return {
          stage: 0,
          pomodoros: 0,
          timer: 0,
          runTimer: false,
      }
  },
  methods: {
      startTimer: function(){
          if(this.runTimer){
              // Timer is already running. We don't let you pause.
              return;
          }

          this.runTimer = true;

          if( this.stage == 0 ){
              // If this is the first time we've hit the button,
              // then we need to pre-set some settings
              this.stage = 1;
              this.timer = 25;
              return;
          }


      },
      pauseTimer: function() {
          this.runTimer = false;
      },
      nextStage: function() {
          this.runTimer = false;

          if(this.stage == 1 && this.pomodoros < 4){
              // Working -> Short Break
              this.stage = 2;
              this.timer = 5;
              return;
          }

          if(this.stage == 1 && this.pomodoros >= 4){
              // Working -> Long Break
              this.stage = 3;
              this.pomodoros = 0;
              this.timer = 15;
              return;
          }

          if(this.stage == 2){
              // Short Break -> Working
              this.stage = 1;
              this.pomodoros++;
              this.timer = 25;
              return;
          }

          if(this.stage == 3){
              this.stage = 1;
              this.timer = 25;
          }


      }
  },
  watch: {
      stage: {
          handler() {
              localStorage.setItem('stage', JSON.stringify(this.stage));
          }
      },
      pomodoros: {
          handler() {
              localStorage.setItem('pomodoros', JSON.stringify(this.pomodoros));
          }
      },
      // timer: {
      //     handler() {
      //         localStorage.setItem('timer', JSON.stringify(this.timer));
      //     }
      // },
      // runTimer: {
      //     handler() {
      //         localStorage.setItem('runTimer', JSON.stringify(this.runTimer));
      //     }
      // },
  },
  mounted() {
      if(localStorage.getItem('stage')){
          this.stage = JSON.parse(localStorage.getItem('stage'));
      }
      if(localStorage.getItem('pomodoros')){
          this.pomodoros = JSON.parse(localStorage.getItem('pomodoros'));
      }
      // if(localStorage.getItem('timer')){
      //     this.timer = JSON.parse(localStorage.getItem('timer'));
      // }
      // if(localStorage.getItem('runTimer')){
      //     this.runTimer = JSON.parse(localStorage.getItem('runTimer'));
      // }
  }
}
</script>

